@import "../../global.scss";

.testimonials {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.6;
    background-color: white;
    overflow: hidden;

    @include mobile {
      flex: 0.4;
    }

    h1 {
      font-size: 40px;
      font-weight: 300;
      margin-left: 30px;
      margin-top: 30px;

      @include mobile {
        align-items: center;
        margin-top: 0px;
        font-size: 20px;
        margin-left: 70px;
      }

      span {
        color: #008080;
        font-size: 60px;

        @include mobile {
          font-size: 30px;
        }
      }
    }

    .container {
      display: flex;
      margin-top: 20px;
      margin-top: 70px;
      width: 90%;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;

      @include mobile {
        align-items: center;
        margin-top: 30px;
        width: 100%;
      }

      .item {
        width: 160px;
        height: 160px;
        margin: 10px 20px;
        display: flex;
        align-items: center;
        cursor: pointer;
        justify-content: center;
        position: relative;
        overflow: hidden;

        @include mobile {
          width: 60px;
          height: 60px;
          margin-top: 10px;
          flex-wrap: wrap;
          position: relative;
        }

        img {
          width: 70%;
          overflow: hidden;
          height: 70%;
          object-fit: cover;
          z-index: 1;

          @include mobile {
            width: 60%;
            height: 60%;
          }
        }
      }
    }
  }

  h4 {
    margin-left: 80px;
    margin-top: 45px;
    font-size: 20px;
    font-weight: 300;

    @include mobile {
      margin-top: 10px;
      font-size: 15px;
      margin-left: 0px;
    }

    .text {
      margin-left: 20px;
    }
  }

  .right {
    flex: 0.4;
    background-color: white;
    overflow: hidden;

    @include mobile {
      align-items: center;
      flex: 0.6;
    }

    .imgContainer {
      width: 550px;
      height: 550px;
      margin-right: 30px;

      @include mobile {
        align-items: center;
        margin-top: 0px;
        width: 350px;
        height: 350px;
      }

      img {
        width: 100%;
        height: 80%;
      }
    }
  }
}
