@import "../../global.scss";

.intro {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;
    overflow: hidden;

    .imgContainer {
      width: 550px;
      height: 550px;
      margin-top: 20px;
      margin-left: 20px;

      @include mobile {
        align-items: center;
        margin-top: 30px;
        width: 350px;
        height: 350px;
      }

      img {
        width: 100%;
        height: 80%;
      }
    }
  }

  .right {
    flex: 0.5;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      flex-direction: column;
      justify-content: center;
      margin-left: 70px;
      display: flex;

      @include mobile {
        padding-left: 0;
        margin-right: 50px;
        align-items: center;
        margin-left: 0px;
      }

      h1 {
        font-size: 70px;
        margin: 30px 0;

        @include mobile {
          font-size: 30px;
          margin: 0 0;
        }
      }
      h2 {
        font-size: 40px;
      }
      h3 {
        font-size: 30px;

        @include mobile {
          font-size: 20px;
        }
      }

      span {
        font-size: inherit;
        color: #008080;
      }

      .ityped-cursor {
        animation: blink 1s infinite;
      }

      @keyframes blink {
        50% {
          opacity: 1;
        }
        100% {
          opacity: 0;
        }
      }
    }
  }
}
