@import "../../global.scss";

.contact {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;
    overflow: hidden;
    margin-left: 20px;
    margin-top: 20px;

    .imgContainer {
      width: 550px;
      height: 510px;
      margin-top: -40px;

      @include mobile {
        align-items: center;
        margin-top: -40px;
        width: 350px;
        height: 300px;
      }

      img {
        width: 100%;
        height: 90%;

        @include mobile {
          height: 87%;
        }
      }
    }
  }

  .right {
    flex: 0.5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-right: 40px;

    @include mobile {
      margin-top: -60px;
      margin-right: 0px;
    }

    h2 {
      font-size: 30px;
      font-weight: 400;

      @include mobile {
        font-size: 18px;
        font-weight: 400;
      }
    }

    form {
      width: 70%;
      height: 70%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;

      @include mobile {
        align-items: center;
        margin-top: -10px;
        width: 110%;
        height: 90%;
      }

      input {
        width: 300px;
        height: 30px;
        font-size: 14px;

        @include mobile {
          width: 200px;
          height: 20px;
          margin-top: 0px;
        }
      }

      textarea {
        width: 300px;
        height: 200px;
        font-size: 14px;

        @include mobile {
          width: 190px;
          height: 100px;
          margin-top: -10px;
        }
      }

      button {
        width: 150px;
        height: 30px;
        color: white;
        background-color: #008080;
        border: none;
        border-radius: 10px;
        font-weight: 500;
        cursor: pointer;

        @include mobile {
          width: 100px;
          margin-top: -10px;
        }

        &:focus {
          outline: none;
        }
      }

      span {
        color: green;
      }
    }
  }
}
